.App {
  max-width: 960px;
  margin: 0 auto;
}
nav {
  display: flex;
  margin: 0 auto 60px;
  gap: 10px;
  align-items: center;
}
nav h1 {
  margin-right: auto;
}
nav a {
  color: #333;
  padding: 4px 10px;
  transition: .5s ease;
  border-bottom: 1px solid black;
  line-height: 15px;
  text-decoration: none;
}
nav a.active{
  background-color: black;
  color: white !important;
  border-radius: 14px;
}